import React from "react";
import {useDispatch} from "react-redux";
import Button from "react-bootstrap/Button";

export default function Header(props){
    const dispatch = useDispatch()
    const addBoards = (board) => {
        if (board != null){
            dispatch({type:'ADD_BOARD', payload: {id: props.boardId, title:`${board}`, items:[]}})
            props.setBoardId(props.boardId + 1)
        }
    }

    return(
        <>
            <h1>Todo App</h1>
            <Button variant="secondary" size="lg"
                onClick={() => addBoards(prompt("Введите название таблицы"))}>Добавить таблицу</Button>
            <div>
                <img src="https://cdn.onlinewebfonts.com/svg/img_103194.png" alt="delete"/>
            </div>

        </>
    )
}